	module.exports = {"data":{"markdownRemark":{"html":"<p>If we noticed that bad weather and expeditions continued for the first time in three weeks I have not seen it for the first time but I got on a <a href=\"/tag/yb-1\">single car</a> . ...... Fire does not enter. This is always bad mood after the rain so push around 1 km. Nevertheless I got sweaty. As I was watching the plug I felt like I felt comfortable, so wipe it clean and call back. (Or I guess the setting is wrong from this motorcycle ... As soon as I lower the plug number I can burn it in white and orz) But it does not matter. Continuously persistently continue pushing but there is no indication. ...... Suddenly thoughtfully I try to fill the gasoline. <em>I went around with a kick one shot</em> . I was doing it for a small hour.</p>","frontmatter":{"cover":null,"date":"2005-09-02T22:52:00+09:00","strDate":"2005/9/2","title":"Then Waibilman"}}},"pathContext":{"path":"/en/blog/2005/09/02-それいけワイビーマン/","langKey":"en"}}

